        >
          <template>
            <v-text-field
                placeholder="Добавить комментарий..."
                outlined
                v-model="message"
                :append-icon="'mdi-send'"
                @click:append="sendMessage"
            >

            </v-text-field>
          </template>


          <v-card
              elevation="0"
              v-for="comment in comments"
              :key="comment.id"
          >
            <v-list-item three-line>
              <v-list-item-avatar
                  left
                  color="#C4C4C4"
                  size="36"
              >
                AA
              </v-list-item-avatar>
              <v-list-item-content>

                <div class="text-subtitle-2 text-left mb-1">
                  <span>{{comment.path}}</span>
                  <span> {{ formatDate(comment.dateCreated) }}</span>
                </div>

                <template>
                  <p class="text-justify mb-0 message-text">
                    {{comment.body}}
                  </p>
                </template>
              </v-list-item-content>

            </v-list-item>

            <v-card-actions class="pa-0 ml-15">
              <v-btn
                  rounded
