<script>
import {mapActions, mapGetters} from "vuex";

export default {
  name: "Comments",

  data() {
    return {
      message: "",
    };
  },

  methods: {
    ...mapActions([
      "fetchComments",
    ]),

    /**
     * Метод отправки сообщения
     */
    sendMessage() {
      if (this.message !== "") {
        console.log("sendMessage", this.message);
        this.message = "";
      }
    },

    /**
     * Метод форматирования даты
     * @param date
     * @returns {string}
     */
    formatDate(date) {
      let newDate = new Date(date)
      const options = {
        year: "numeric",
        month: "2-digit",
        day: "2-digit",
        hour: "2-digit",
        minute: "2-digit",
        second: "2-digit",
      };
      return new Intl.DateTimeFormat("ru-Ru", options).format(newDate);
    }
  },
  computed: {
    ...mapGetters({
      comments: "COMMENTS",
    }),
  },
  async mounted() {
    await this.fetchComments();
  },
};
</script>

